.badge {
    @apply bg-gray-300 py-1 px-3 text-sm font-medium rounded-full whitespace-no-wrap;

    @each $theme, $color in $colors-badge {
        &-#{$theme} {
            background-color: $color;
            color: color-yiq($color);

            &-outline {
                border: 1px solid $color;
                color: $color;
                @apply bg-white;
            }
        }
    }

    @each $element, $size in $sizes {
        &-#{$element} {
            font-size: $size;
            line-height: $size;
            padding: ($size * 0.4) ($size * 0.8);
        }
    }
}
